<template>

  <header class="blur">
    <nav class="container">
      <div class="branding">
        <router-link class="header" :to="{ name: 'Home' }">Shared Spirits</router-link>
      </div>
      <div class="nav-links">
        <ul v-show="!mobile">
          <router-link class="link" :to="{ name: 'Home' }">Home</router-link>
          <router-link class="link" :to="{ name: 'Home' }">My Drinks</router-link>
          <router-link class="link" :to="{ name: 'Home' }">Parties</router-link>
          <router-link class="link" :to="{ name: 'Login' }">Login/Register</router-link>
        </ul>
        <div :class="{ 'mobile-user-menu': mobile }" @click="toggleProfileMenu" class="profile" ref="profile">
          <span>Gs</span>
          <div v-show="profileMenu" class="profile-menu">
            <div class="info">
              <p class="initials">Gs</p>
              <div class="right">
                <h4>Gagan</h4>
               <div class="orders"> 
                <table>
                    <tr>
                    <td>Sent</td>
                    <td>4</td>
                    </tr>
                    <tr>
                        <td>Recived</td>
                        <td>13</td>
                        </tr>
                    </table>
                </div>
              </div>
            </div>
            <div class="options">
              <div class="option">
                <router-link class="option" :to="{ name: 'Profile' }">
                  <userIcon class="icon" />
                  <p>Edit Profile</p>
                </router-link>
              </div>
              <div  class="option">
                <signOutIcon class="icon" />
                <p>Sign Out</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </nav>
    <menuIcon @click="toggleMobileNav" class="menu-icon" v-show="mobile" />
    <transition name="mobile-nav">
      <ul class="mobile-nav" v-show="mobileNav">
        <router-link class="link" :to="{ name: 'Home' }"><homeIcon class="icons"/>Home</router-link>
        <router-link class="link" :to="{ name: 'Home' }"><drinkIcon class="icons"/>My Drinks</router-link>
       <router-link class="link" :to="{ name: 'Home' }"><searchIcon class="icons"/>Search</router-link>
      <router-link class="link" :to="{ name: 'Home' }"><discoverIcon class="icons"/>Discover</router-link>
       <router-link class="link" :to="{ name: 'Home' }"><shareIcon class="icons"/>Share</router-link>
      <router-link class="link" :to="{ name: 'Home' }"><partyIcon class="icons"/>Parties</router-link>
       <router-link class="link" :to="{ name: 'Home' }"><ticketIcon class="icons"/>My Tickets</router-link>
        <router-link class="link" :to="{ name: 'Home' }"><favoriteIcon class="icons"/>Favorites</router-link>
         <router-link class="link" :to="{ name: 'Home' }"><statementIcon class="icons"/>Statement</router-link>
          <router-link class="link" :to="{ name: 'Home' }"><howIcon class="icons"/>How To USe</router-link>
           <router-link class="link" :to="{ name: 'Home' }"><termIcon class="icons"/>Terms & Conditions</router-link>
           <router-link class="link" :to="{ name: 'Login' }"><loginIcon class="icons"/>Login/Register</router-link>
      
      </ul>
    </transition>
    
  </header>
 
</template>

<script>
import menuIcon from "../assets/Icons/bars-regular.svg";
import userIcon from "../assets/Icons/user-alt-light.svg";
import signOutIcon from "../assets/Icons/sign-out-alt-regular.svg";
import homeIcon from "../assets/Icons/home-white24.svg";
import drinkIcon from "../assets/Icons/drink.svg"
import searchIcon from "../assets/Icons/search.svg"
import discoverIcon from "../assets/Icons/discover.svg"
import partyIcon from "../assets/Icons/party.svg"
import favoriteIcon from "../assets/Icons/favorites.svg"
import ticketIcon from "../assets/Icons/ticket.svg"
import shareIcon from "../assets/Icons/share.svg"
import statementIcon from "../assets/Icons/statement.svg"
import howIcon from "../assets/Icons/how.svg"
import termIcon from "../assets/Icons/term.svg"
import loginIcon from "../assets/Icons/login.svg"

export default {
  name: "navigation",
  components: {
    menuIcon,
    userIcon,
   signOutIcon,
   homeIcon,
   drinkIcon,
   discoverIcon,
   shareIcon,
   searchIcon,
   ticketIcon,
   favoriteIcon,
   termIcon,
   howIcon,
   statementIcon,
   partyIcon,
   loginIcon,
  },
  data() {
    return {
      profileMenu: null,
      mobile: null,
      mobileNav: null,
      windownWidth: null,
    };
  },
  created() {
    window.addEventListener("resize", this.checkScreen);
    this.checkScreen();
  },
  methods: {
    checkScreen() {
      this.windownWidth = window.innerWidth;
      if (this.windownWidth <= 750) {
        this.mobile = true;
        return;
      }
      this.mobile = false;
      this.mobileNav = false;
      return;
    },
    toggleMobileNav() {
      this.mobileNav = !this.mobileNav;


    },
    toggleProfileMenu(e) {
      if (e.target === this.$refs.profile) {
        this.profileMenu = !this.profileMenu;
      }
    },
  
  },
 
};
</script>

<style lang="scss" scoped>
header {
  background-color: rgb(17, 102, 212);
  padding: 0 20px;
  box-shadow: 0 4px 6px -1px rgb(233, 229, 229), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  z-index: 99;
   
  .link {
    font-weight: 500;
    padding: 0 8px;
    transition: 0.3s color ease;
    color: #ffffff;
    &:hover {
      color: #a10f0f;
    }
  }
  nav {
    display: flex;
    padding: 15px 0;
    .branding {
      display: flex;
      align-items: center;
      .header {
        font-weight: 500;
        font-size: 20px;
        color: rgb(255, 255, 255);
        text-decoration: none;
        
      }
    }
    .nav-links {
      position: relative;
      display: flex;
      flex: 1;
      align-items: center;
      justify-content: flex-end;
     color: rgb(255, 255, 255);

      ul {
        margin-right: 32px;
        .link {
          margin-right: 32px;
        }
        .link:last-child {
          margin-right: 0;
        }
      }
      .profile {
        position: relative;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        color: #fff;
        background-color: #303030;
        span {
          pointer-events: none;
        }
        .profile-menu {
          position: absolute;
          top: 60px;
          right: 0;
          width: 250px;
          background-color: #303030;
          box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
          .info {
            display: flex;
            align-items: center;
            padding: 15px;
            border-bottom: 1px solid #fff;
            .initials {
              position: initial;
              width: 40px;
              height: 40px;
              background-color: #fff;
              color: #303030;
              display: flex;
              align-items: center;
              justify-content: center;
              border-radius: 50%;
            }
            .right {
              flex: 1;
              margin-left: 24px;
              p:nth-child(1) {
                font-size: 14px;
              }
              .orders{
                 margin-left: 5px;
                  padding: 3px;
                  display: flex;
                  flex-direction: column;

              }
              p:nth-child(2),
              p:nth-child(3) {
                font-size: 12px;
              }
            }
          }
          .options {
            padding: 15px;
            .option {
              text-decoration: none;
              color: #fff;
              display: flex;
              align-items: center;
              margin-bottom: 12px;
              .icon {
                width: 18px;
                height: auto;
               
              }
              p {
                font-size: 14px;
                margin-left: 12px;
              }
              &:last-child {
                margin-bottom: 0px;
              }
            }
          }
        }
      }
    }
  
    .mobile-user-menu {
      margin-right: 40px;
       
    }
  }
  .menu-icon {
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 25px;
    height: 25px;
    width: auto;
  
  }

  .mobile-nav {
    padding: 5px;
    width: 70%;
    max-width: 250px;
    display: flex;
    flex-direction: column;;
    position: fixed;
    height: 100%;
    background-color:#ffffff;
    
    top: 0;
    left: 0;
   
   
   .icons{
    margin-right:5px;
   fill: blue;
  
   }
    .link {
      display: flex;
      flex-direction: row;
     font-size: 20px;
    padding: 10px 0;
      color: rgb(17, 102, 212);
   
  border-bottom: 1px solid black;
      
      
    }
    
  }
  .mobile-nav-enter-active,
  .mobile-nav-leave-active {
    transition: all 1s ease;
    
  }
  .mobile-nav-enter {
    transform: translateX(-250px);
   
  }
  .mobile-nav-enter-to {
    transform: translateX(0);
    
  }
  .mobile-nav-leave-to {
    transform: translateX(-250px);
  }
}
</style>